import React, { useEffect, useState } from 'react'
import { songList } from './songList';


const Player = props => {
    const [play, setPlay] = useState(false);
    const [songlt, setSonglt] = useState(100);
    const [position, setPosition] = useState(0);
    const [songName, setSongName] = useState('');
    const [menu, setMenu] = useState(true);
    const reader = document.getElementById("pista_reader");
    

    const [song, setSong] = useState(songList[0]);
    


    useEffect(() => {
        let ara = song.split('/');
        setSongName(ara[ara.length - 1]);
        reader && hadleEvent(reader);
    }, [reader, song])
    function handlePosition({target}) {
        // console.log(target)
        setPosition(target.value)
        reader.currentTime = target.value

    }
    function hadlePlay() {
        // const reader = document.getElementById("pista_reader");

        if (reader) {
            setSonglt(reader.duration)
            // setPlay(true);
            reader.play();

        }
    }
    function hadlePause() {
        const reader = document.getElementById("pista_reader");
        if (reader) {
            reader.pause();
            // setPlay(false);
            clearInterval();
        }

    }
    function handleLoad() {
        // const reader = document.getElementById("pista_reader");
        if (reader) {
            reader.load();
            // reader.play()
            // setPlay(true);
        }
    }
    function handleNext() {
        let indexCS = songList.indexOf(song);
        if (songList[indexCS + 1]) {
            setSong(songList[indexCS + 1]);
            setTimeout(() => {
                reader.play();
                setSonglt(reader.duration);
            }, 1000);
            // setPlay(true);
            return true
        } else
            return false

    }
    function handleBefore() {
        let indexCS = songList.indexOf(song);
        if (songList[indexCS - 1]) {
            setSong(songList[indexCS - 1]);
            setTimeout(() => {
                reader.play();
                setSonglt(reader.duration);
            }, 1000);
            // setPlay(true);
        }

    }
    function handleInit() {
        if(!handleNext()){
            reader.load()
        }
    }
    function hadleEvent(reader) {
        reader.addEventListener('timeupdate', () => {
            return setPosition(reader.currentTime);
        })
        reader.addEventListener('ended', () => {
            return handleInit();
        })
        reader.addEventListener('pause', () => {
            return setPlay(false);
        })
        reader.addEventListener('play', () => {
            return setPlay(true);
        })
        reader.addEventListener('loadeddata', () => {
            return setPlay(false);
        })


    }
    function handlePlaySong(src){
        setSong(src);
        setTimeout(()=>{
            hadlePlay();
        }, 1000)

    }


    return (
        <div className="player">
            <div className="player-container">
                <div className={ menu ? "song-list move-menu-left": "song-list"}>
                    {
                        songList.map((song)=>{
                            let src = song.split('/');
                            return <div key={src} className="song-item" onClick={()=>handlePlaySong(song)}>{src[src.length-1]}</div>;
                        })
                    }
                </div>
                <div className="player-header">
                    <i className="fas fa-ellipsis-v"></i>
                    <h4 className="song-title">{songName}</h4>
                    <i className="fas fa-music" onClick={()=>menu ? setMenu(false) : setMenu(true)}></i>
                </div>
                <div className="player-body">
                    {
                        play
                            ? <div className="circle boderC">
                                <div className="figura spin-disc"></div>
                            </div>
                            : <div className="circle">
                                <div className="figura"></div>
                            </div>
                    }
                </div>
                <div className="player-footer">
                    <input type="range" id="slide" className="slide" name="slaide" min={0} max={songlt} value={position} onChange={handlePosition} />
                    <i className="fas fa-redo-alt" onClick={handleLoad}></i>
                    <div className="media-group">
                        <audio id="pista_reader" src={song}></audio>
                        <i className="fas fa-fast-backward" onClick={handleBefore}></i>
                        {
                            (play)
                                ? <i className="fas fa-pause-circle" onClick={hadlePause}></i>
                                : <i className="fas fa-play-circle" onClick={hadlePlay}></i>
                        }
                        <i className="fas fa-fast-forward" onClick={handleNext}></i>

                    </div>
                </div>

            </div>
        </div>
    )
}

export default Player
